export const searchCricketUsers = (page, name = '', type = '', status = '', dataPerPage = '') => {
  return async (dispatch) => {
    await http.get(`/cricket/series/list?page=${page}&series_name=${name}&type=${type}&series_status=${status}&limit=${dataPerPage}`).then((response) => {
      dispatch({
        type: 'SEARCH_CRICKET_USERS',
        data: response.data.data
      })
    })
    .catch((err) => console.log(err))
  }
}

