import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label
  //CustomInput
} from 'reactstrap' 
import Flatpickr from 'react-flatpickr'
import BreadCrumbs from '@components/breadcrumbs'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import React, { useState, useEffect, Fragment } from 'react'
import { viewfootballseries, editfootballseries } from '../../store/actions'
import { useDispatch, useSelector } from 'react-redux'
import '@styles/react/libs/flatpickr/flatpickr.scss'


const selectstatus = [
  { value: '1', label: 'Live' },
  { value: '2', label: 'Upcoming' },
  { value: '3', label: 'Completed' },
  { value: '4', label: 'Cancelled' }
]
const selecttype = [
  { value: 'ODI', label: 'ODI' },
  { value: 'T20I', label: 'T20I' },
  { value: 'WOMANODI', label: 'WOMANODI' },
  { value: 'T20', label: 'T20' }
]
const VerticalForm = () => {
  
  const dispatch = useDispatch()
  const store = useSelector(state => state.series)
  const {view_football_series} = store
  console.log(view_football_series)
  
  const [editseriesform, setEditseriesform] = useState()
  
  useEffect(() => {
    dispatch(viewfootballseries)
    //console.log("Ravi", props.id)
  }, [view_football_series])
  useEffect(() => {
    setEditseriesform({series_name:view_football_series?.series_name, series_api_id:view_football_series?.series_api_id, series_sh:view_football_series?.series_sh, date_time:view_football_series?.date_time, series_status:view_football_series?.series_status, series_logo:view_football_series?.series_logo, type:view_football_series?.type, active:view_football_series?.active})
  }, [view_football_series])
  console.log(view_football_series?.series_name)
  console.log(editseriesform)
  const onSubmit = (e) => {
    e.preventDefault()
    console.log(editseriesform)
    dispatch(editfootballseries(view_football_series.id, editseriesform))
  }
  
 const editcategory = (e) => {
    
    setEditseriesform({...editseriesform, series_status:e.value})
  }
  const typecategory = (e) => {
    
    setEditseriesform({...editseriesform, type:e.value})
  }


  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Football Series' breadCrumbParent='Footballseries' breadCrumbActive='Edit' />
    <Card>
    <CardHeader>
      <CardTitle tag="h4">Edit Football Series</CardTitle>
    </CardHeader>
    <CardBody>
      <Form>
        <Row>
        <Col sm="12">
            <FormGroup>
              <Label for="EmailVertical">series Name</Label>
              <Input
                type="name"
                name="Email"
                id="EmailVertical"
                value={editseriesform?.series_name}
                onChange={(e) => setEditseriesform({ ...editseriesform, series_name: e.target.value })}
                placeholder="Enter No of ID "
              />
            </FormGroup>
          </Col>
          <Col sm="12">
            <FormGroup>
              <Label>series_api_id</Label>
              <Input
                type="number"
                name="name"
                id="nameVertical"
                value={editseriesform?.series_api_id}
                onChange={(e) => setEditseriesform({ ...editseriesform, series_api_id: e.target.value })}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>
          <Col>
            <FormGroup>
              <Label for="nameVertical">series_sh</Label>
              <Input
                type="text"
                name="name"
                id="nameVertical"
                value={editseriesform?.series_sh}
                onChange={(e) => setEditseriesform({ ...editseriesform, series_sh: e.target.value })}
                placeholder="Enter Product Name"
              />
            </FormGroup>
          </Col>
          <Col sm="12" >
            <FormGroup>               
              <Label for="date-time-picker">date Time</Label>
              <Flatpickr
                data-enable-time
                id="date-time-picker"
                className="form-control"
                // onChange={(date) => edittime(date)}
                // key={date}
                value={editseriesform?.date_time}
                options={{
                  enableTime: true,
                  dateFormat: "Y-m-d H:i:s",
                  // defaultDate :new Date(editprizeform?.expires_at)
                  // defaultDate: editprizeform?.expires_at
                  defaultDate: editseriesform ? editseriesform.date_time : new Date()
                  // defaultDate: date
                  // defaultDate: '2016-10-22'
                }}
                onChange={(selectedDates, dateStr) => {
                  const firstDate = selectedDates[0]
                  setEditseriesform({ ...editseriesform, date_time:dateStr})
                  console.log({firstDate, dateStr })
                }}
              />
              </FormGroup>
              </Col>
          <Col sm="12" >
            <FormGroup>
              <Label for="nameVertical">series_status</Label>
              <Select
                theme={selectThemeColors}
                className="react-select"
                classNamePrefix="select"
                defaultValue={selectstatus[0]}                 
                onChange={(e) => editcategory(e)}
                options={selectstatus}
                isClearable={false}
              />
            </FormGroup>
          </Col>
          <Col sm="12">
            <FormGroup>
              <Label for="EmailVertical">Series Logo</Label>
              <Input
                type="number"
                name="Email"
                id="EmailVertical"
                value={editseriesform?.series_logo}
                onChange={(e) => setEditseriesform({ ...editseriesform, series_logo: e.target.value })}
                placeholder="Enter No of Coins "
              />
            </FormGroup>
          </Col>
          <Col>
            <FormGroup>
              <Label for="nameVertical">Type</Label>
              <Select
                theme={selectThemeColors}
                className="react-select"
                classNamePrefix="select"
                defaultValue={selecttype[0]}                 
                onChange={(e) => typecategory(e)}
                options={selecttype}
                isClearable={false}
              />
            </FormGroup>
          </Col>
          <Col sm="12">
            <FormGroup>
              <Label for="EmailVertical">Active</Label>
              <Input
                type="number"
                name="Email"
                id="EmailVertical"
                value={editseriesform?.active}
                onChange={(e) => setEditseriesform({ ...editseriesform, active: e.target.value })}
                placeholder="Enter No of ID "
              />
            </FormGroup>
          </Col>
        
        
          <Col sm="12">
            <FormGroup className="d-flex mb-0">
              <Button.Ripple
                className="mr-1"
                color="primary"
                type="submit"
                onClick={(e) => onSubmit(e)}
              >
                Submit
              </Button.Ripple>
              {/* <Button.Ripple
                outline
                color="secondary"
                type="reset"
                onClick={(e) => onclear(e)}
              >
                Reset
              </Button.Ripple> */}
            </FormGroup>
          </Col>
        </Row>
      </Form>
    </CardBody>
  </Card>
  </Fragment>
  )
}
export default VerticalForm
